import React from 'react'
import ApplicationItem from '../Components/ApplicationItem'
import PropertyItem from '../Components/PropertyItem'
import AddPropertyFormComponent from '../Components/AddPropertyForm'
import activeClass from '../helpers/active-class'
import addPropertyForm from '../forms/addPropertyForm'

const MobileView = ({
  resource,
  setQuery,
  applications,
  properties,
  showModal,
  hideModal,
  applicationsRefetch,
  refetch
}) => (
  <div className="list-group h-100">
    <div className="list-group-item bg-secondary bb-0">
      <div className="d-flex align-items-center justify-content-between height-35">
        {resource === 'applications' && [
          <header
            key="1"
            className={activeClass(resource === 'applications', "d-flex text-white")}
            onClick={() => setQuery({ resource: 'applications' })}
            children="Applications"
          />,
          <header
            key="2"
            className={activeClass(resource === 'properties', "d-flex text-white")}
            onClick={() => setQuery({ resource: 'properties' })}
            children="Properties"
          />
        ]}

        {resource === 'properties' && [
          <div className="d-flex align-items-center">
            <header
              key="2"
              className={activeClass(resource === 'properties', "d-flex text-white mr-3")}
              onClick={() => setQuery({ resource: 'properties' })}
              children="Properties"
            />
            <button
              type="button"
              className="btn btn-sm btn-dark"
              children="+ Add Property"
              onClick={() => showModal({ children: AddPropertyFormComponent({ fields: addPropertyForm, refetch }) })}
            />
          </div>,
          <header
            key="1"
            className={activeClass(resource === 'applications', "d-flex text-white")}
            onClick={() => setQuery({ resource: 'applications' })}
            children="Applications"
          />,
        ]}
      </div>
    </div>

    {resource === 'applications' && applications && (
      <div>
        {applications.map(ApplicationItem({ showModal }))}
      </div>
    )}

    {resource === 'properties' && properties && (
      <div className="card-columns p-3">
        {properties.map(PropertyItem({ showModal, hideModal, applicationsRefetch, refetch }))}
      </div>
    )}

  </div>
)

export default MobileView



