import React from 'react'
import Form from '../Components/Form'
import addPropertyValidator from '../validators/property-validator'
import { addPropertyHandler } from '../handlers/properties'

let AddPropertyForm = ({ fields, refetch }) => ({ showModal, hideModal }) => (
  <div>
    <button
      type="button"
      className="close p-4"
      aria-label="Close"
      onClick={hideModal}
    >
      <span aria-hidden="true">&times;</span>
    </button>
    <Form
      className="p-4"
      validate={addPropertyValidator}
      onSubmit={addPropertyHandler({ hideModal, refetch })}
      fields={fields}
      meta={{ showModal, hideModal }}
    />
  </div>
)

export default AddPropertyForm


