import React from 'react'
import { NavLink, Link, withRouter } from 'react-router-dom'
import Logo from '../Components/images/inlineLogo.png'

const AppBar = ({
  history,
  user = {},
  sample = true,
}) => (
  <nav 
    style={{ flexShrink: 0 }}
    className="d-block d-md-flex nav-pills pl-2 pb-2 pr-2 bg-white border align-items-center"
  >
    <img
      alt=""
      className="cursor-pointer"
      onClick={() => history.push('/')}
      style={{ marginRight: 20, height: 80 }}
      src={Logo}
    />
    <div className="d-flex justify-content-between f-1">
      <div className="d-flex flex-row pt-3">
        {sample && (
          <div className="btn-group">
            <button type="button" className="btn btn-info dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              Example Report
            </button>
            <div className="dropdown-menu">
              <Link
                className="dropdown-item"
                to="/application1"
                children="Application"
              />
              <Link
                className="dropdown-item"
                to="/example"
                children="Report"
              />
            </div>
          </div>
        )}
      </div>
      <div className="d-flex flex-row pt-3">
        {localStorage.getItem('auth') ? ([
          <NavLink
            key="1"
            className="nav-link d-flex align-items-center mr-2"
            to="/dashboard"
            children="Dashboard"
            exact
          />,
          <NavLink
            key="2"
            className="nav-link d-flex align-items-center bg-danger text-white"
            to="/login"
            onClick={() => localStorage.removeItem('auth')}
            children="Logout"
          />
        ]) : ([
          <NavLink
            key="1"
            className="nav-link d-flex align-items-center mr-2"
            to="/login"
            children="Login"
            exact
          />,
          <NavLink
            key="2"
            className="nav-link d-flex align-items-center"
            to="/signup"
            children="Signup"
            exact
          />
        ])}
      </div>
    </div>
  </nav>
)

export default withRouter(AppBar)


